import React, { Component } from 'react';
import {
  CButton,
  CCard,
  CCardBody,
  CCol,
  CContainer,
  CSelect,
  CInput,
  CInputGroup,
  CRow,
} from '@coreui/react'
import "src/scss/profile.scss"
import {Base_URL} from "../../CommonRoutes/Api"
import BlockUI from 'src/CommonRoutes/BlockUI/BlockUI';

class UpdateProfile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      statename: [],
      state:'',
      cityname: [],
      city: '',
      address:'',
      pincode:'',
      profile:[],
      id:'',
      profile1:[],
      profile2:[],
      userid:localStorage.getItem("user_id"),
      mobile:'',
      name:'',
      email:'',
      blocking:false

    }
  }

  componentDidMount() {

    if((!localStorage.getItem('token')) || (!localStorage.getItem('role')) || (!localStorage.getItem('user_id')))
    {
      this.props.history.push("/");
    }

       this.setState({blocking:true})
      fetch(Base_URL + "allstates").then((response) => {
        response.json().then((result) => {
          console.log(result);
          this.setState({ statename: result.data,blocking:false})
          setTimeout(
            () => this.setState({ position: 1,blocking:false }), 
            3000
          );
        })
      })

      

       this.setState({blocking:true})
      fetch(Base_URL + "getuserbyid/" +this.state.userid).then((response) => {
        response.json().then((result) => {
            console.log("result",result);
            this.setState({profile:result,blocking:false})
            console.log("profile",this.state.profile);
            console.log("profile1",this.state.profile[0]);
            console.log("profile2",this.state.profile[1][0]);
            this.setState({profile1:this.state.profile[0],profile2:this.state.profile[1][0]})
             this.setState({
              name:this.state.profile1.name,
              mobile:this.state.profile1.mobile,
              address:this.state.profile2.address,
              pincode:this.state.profile2.pincode,
              email:this.state.profile1.email,
              state:this.state.profile2.state,
              city:this.state.profile2.city,
              id:this.state.profile1.id
            })  
            setTimeout(
              () => this.setState({ position: 1,blocking:false }), 
              3000
            );
        }) 
        
    })        
} 






  changeState = (e) => { 
    this.setState({cityname : "",blocking:true});
     this.setState({ state: e.target.value});
    fetch(Base_URL + 'getCitybyStateid/' +e.target.value).then((resp) => {
      resp.json().then((result) => {
        this.setState({ cityname: result.data,blocking:false})
        setTimeout(
          () => this.setState({ position: 1,blocking:false }), 
          3000
        );
      })
      
    })
  } 

 

 update()
{ 
  const data = { name:this.state.name,mobile:this.state.mobile,email:this.state.email,address:this.state.address,state:this.state.state,city:this.state.city,pincode:this.state.pincode}
    fetch(Base_URL + "update_userinfo/" + this.state.userid,
    {
        method:"Put",
        headers: {
            
            'Content-Type': 'application/json',
          },
        body:JSON.stringify(data)
    }).then((result)=> {
             result.json().then((resp)=> {
         alert("data is updated");
         this.props.history.push('/myprofile');  

        })

    })
     
 } 
 


 
 
  render() {
    let inputStyle = {};
    if(this.state.state > 0) {
      inputStyle = {
           backgroundColor:"rgb(232, 240, 254)",borderRadius: '30px',border: '1px solid #3B5998',color:"black"
      } 
    } else{
      inputStyle = {
        borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998'
   } 
    } 

    let inputStyle1 = {};
    if(this.state.city !== null) {
      inputStyle1 = {
           backgroundColor:"rgb(232, 240, 254)",borderRadius: '30px',border: '1px solid #3B5998',color:"black"
      } 
    } else{
      inputStyle1 = {
        borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998'
   } 
    } 
    console.log(this.props);
    return (
      <div className="c-app c-default-layout flex-row align-items-center">
        <CContainer style={{padding:"10px",marginTop:"-1.6rem"}}>
          <CRow className="justify-content-center">
            <CCol md="12" lg="12" xl="12" style={{ padding: "0px" }}>
              <CCard className="mx-10"  style={{ backgroundColor: '#110730', marginLeft: '-1rem', marginRight: '-.6rem', marginBottom: '-.1rem' }}>
                <CCardBody className="p-10">
                  {/* <CForm >  */}
                  <center><h4 style={{color:"orange"}}>Update Profile Info</h4></center>
                  <label style={{color:"white"}}>Name</label>
                  <CInputGroup className="mb-3" >
                    <CInput type="text"
                      name="name"
                      placeholder="Name"
                      autoComplete="false"
                     value={this.state.name}
                     onChange={(event) =>{this.setState({name:event.target.value})}}
                     style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                    />
                  </CInputGroup>
                  <label style={{color:"white"}}>Mobile Number</label>
                  <CInputGroup className="mb-3" >
                    <CInput type="tel"
                      name="mobile"
                      maxLength="10"
                      minLength="10"
                      placeholder="Mobile Number"
                      autoComplete="false"
                      value={this.state.mobile}
                      onChange={(event) =>{this.setState({mobile:event.target.value})}}
                      style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                    />
                  </CInputGroup>
                  <label style={{color:"white"}}>Email</label>
                  <CInputGroup className="mb-3">
                    <CInput type="email"
                      name="email"
                      placeholder="Email"
                      autoComplete="false"
                     value={this.state.email}
                    // onChange={(event=>{this.setState({email:event.target.value})})}
                    style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                    />
                  </CInputGroup>
                  <label style={{color:"white"}}>Address</label>
                  <CInputGroup className="mb-3">
                    <CInput type="text"
                      name="address"
                      placeholder="Address"
                      autoComplete="false"
                      value={this.state.address}
                      onChange={(event=>(this.setState({address:event.target.value})))}
                      style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                    />
                  </CInputGroup>
                  <label style={{color:"white"}}>State</label>
                  <CInputGroup className="mb-3">
                    <CSelect value=""
                      id="select" selected
                     value={this.state.state}
                      onChange={this.changeState}
                      style={inputStyle}
                      name="statename">
                         <option>Select State</option> 
                      {
                        this.state.statename.map((item) => {
                         return  <option item={item} default value={item.state_name} value={item.id}>{item.state_name}</option>;

                        })}
                    </CSelect>
                  </CInputGroup>
                  <label style={{color:"white"}}>City</label>
                  <CInputGroup className="mb-3">
                    <CSelect value=""
                      name="cityname"
                      id="select"
                      value={this.state.city}
                      onChange={(event) => { this.setState({ city: event.target.value }) }}
                      style={inputStyle1}
                    
                    >
                      <option>Select city</option> 
                    
                      {this.state.cityname != 0 ?
                         this.state.cityname.map((item, i) => {
                       return   <option item={item} value={item.city_name}>{item.city_name}</option>

                      }) : <span>No city Available</span>}


                    </CSelect>
                  </CInputGroup>
                  <label style={{color:"white"}}>Pincode</label>
                  <CInputGroup className="mb-3">
                    <CInput type="number"
                      name="pincode"
                      placeholder="Pincode"
                      autoComplete="false"
                      value={this.state.pincode}
                      required="true"
                      onChange={(event)=>(this.setState({pincode:event.target.value}))}
                      style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                    />

                  </CInputGroup>
                  <span style={{ float: "right", marginTop: "-1rem", marginBottom: "0.8rem",color:"white"}}>6/6</span>

                <CButton
                    type="submit"
                    value="add"
                    block color="warning"
                    fullidth={true}
                    onClick={()=>this.update()}
                  >Update Info</CButton>
                  {/*   </CForm>  */}
                </CCardBody>
              </CCard>
            </CCol>
          </CRow>
        </CContainer>
        <BlockUI blocking={this.state.blocking} />
      </div>
    );
  }
}

export default UpdateProfile;