import React from 'react';
import {
    CCard,
    CCardBody,
    CCol,
    CRow,
    CCardHeader
  } from '@coreui/react'
  import CIcon from '@coreui/icons-react';
import "../../scss/Card.scss";
import C9 from "../../assets/img/c2.jpg";
import {Base_URL} from "../../CommonRoutes/Api"
import g6 from "../../assets/img/im2.jpg"
import g7 from "../../assets/img/im3.jpg"

function ProductList(props) {
    return (
        <div>
           <CCard style={{marginLeft: "-0.5rem",
      marginRight: "-0.5rem",marginTop: "0.9rem"}}>
              <CCardHeader>
               <center><img src={g6} height="150px" width="80%" /></center> 
             
              </CCardHeader>
              <CCardBody>
                 <center>{props.name}</center>
              </CCardBody>
            </CCard>
        </div>
    );
}

export default ProductList;

