import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import Paper from "@material-ui/core/Paper";
import "../../scss/Slid.scss";
import { Base_URL, imgPath } from '../../CommonRoutes/Api'
import g1 from "../../assets/img/1.jpg"
// import n1 from "../../../src/assets/img/Screen.png"
import BlockUI from 'src/CommonRoutes/BlockUI/BlockUI';


class UploadPhoto extends Component {
constructor(props) {
super(props);
this.state = {
load: true,
category: [],
blocking: false,
lat: "",
long: ""
};
}

componentDidMount() {


this.setState({ blocking: true })
fetch(Base_URL + "allCategories").then((response) => {
response.json().then((result) => {
console.log(result.data);
this.setState({ category: result.data, blocking: false })
setTimeout(
() => this.setState({ position: 1, blocking: false }),
3000
);
})
})
};
render() {
return (

<div
className="sliderContainer"
// style={{ width: "100%", height: "100%" }}
>

{
this.state.category && this.state.category.length > 0
?
this.state.category.slice(0, 5).map((res) => (

<div
className="sliderCategoryCard" style={{ height: "10%", width: "19.7%" }}
key={res.id}
>

<div

style={{ height: "100%", width: "100%", borderRadius: "50px", marginBottom: "1rem" }}
>
<img src={g1} alt="" width="100%" height="100%" style={{
paddingTop: "1rem"

}} />
</div>



</div>
))
: "no data found"}

<BlockUI blocking={this.state.blocking} />
</div>


);
}
}

export default UploadPhoto;
