import axios from 'axios';
import {Base_URL} from './Api'

const user_id=localStorage.getItem("user_id")

export function getVendor() {
	return axios.get(Base_URL+"allvendortype")
		.then(response => response.data);
}

export function getCompany() {
	return axios.get(Base_URL+"allcompanytype")
		.then(response => response.data);
}

export function getCitybyState(id) {
	return axios.get(Base_URL+"getCitybyStateid/"+id)
		.then(response => response.data);
}

export function getState() {
	return axios.get(Base_URL+"allstates")
		.then(response => response.data);
}

export function getProfile(user_id) {
	return axios.get(Base_URL+"getuserbyid/"+ user_id)
		.then(response => response.data);
}

export function getProduct() {
	return axios.get(Base_URL+"getproducts_vendor_id/"+ user_id)
		.then(response => response.data);
}

export function getOrder(option) {
	return axios.get(Base_URL+"invoices_by_status_V/"+ option +"/" + user_id)
		.then(response => response.data);
}

export function getCategory() {
	return axios.get(Base_URL+"allCategories")
		.then(response => response.data);
}

export function getSubCategorybyID(e) {
	return axios.get(Base_URL+"getSubCategorybycatid/"+e)
		.then(response => response.data);
}

