import React from 'react';
import {
    CCard,
    CCardBody,
    CCol,
    CRow,
    CCardHeader,
    CButton,
    CLink
  } from '@coreui/react'
  
  //import { NavLink } from 'react-router-dom';
  import CIcon from '@coreui/icons-react';
  import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPhoneAlt, faStar } from '@fortawesome/free-solid-svg-icons';
import "../../scss/Card.scss";
import C9 from "../../assets/img/c2.jpg";
import {Base_URL} from "../../CommonRoutes/Api"
import a1 from "../../assets/img/items/2.jpg"
import g7 from "../../assets/img/im3.jpg"

function Vendor(props) {

  
  
    return (
        <div>
            {props.data.map((item) => (
            <CCard  className=""
          style={{padding: "0px",
    marginTop: "0.9rem",
    marginLeft: "-1rem",
    marginRight: "-0.9rem",
    marginBottom: "-0.2rem"}} >
          <CCardBody style={{display:"flex"}}>
          <CCol xs="4" sm="4" md="4" className="card_col">
         
      <img src={g7} alt="no images found" width="100%" height="155px"style={{
    float: "left",
    marginTop: "-1rem",
    marginBottom: "-1rem",
    marginLeft: "-1.3rem",
    borderRadius: "7px"
    }}/> 
          </CCol>
          <CCol xs="8" sm="8" md="8" className="card_col" style={{marginTop: "-1rem",textAlign:"left"
    }}>
          
         <p style={{color: "black",fontSize:"15px",
    fontWeight: "bold",marginBottom:"0px"}}>{item.DisplayName} </p> 
   <p style={{marginBottom:"0px",fontSize:"15px"}}>  <span style={{fontSize:"15px"}}><FontAwesomeIcon icon={faStar} style={{color:"yellow"}}/></span>
    <span style={{fontSize:"15px"}}><FontAwesomeIcon icon={faStar} style={{color:"yellow"}}/></span>
    <span style={{fontSize:"15px"}}><FontAwesomeIcon icon={faStar} style={{color:"yellow"}}/></span>
    <span style={{fontSize:"15px"}}><FontAwesomeIcon icon={faStar} style={{color:"yellow"}}/></span>
    <span style={{fontSize:"15px"}}><FontAwesomeIcon icon={faStar} style={{color:"yellow"}}/></span>5 Rating</p>
            <p style={{display: "block",
    fontSize: "16px",
    marginBottom: "0px",
    color: "gray"}}>{item.Address}</p>
      <p style={{display: "block",
    fontSize: "16px",
    marginBottom: "0px",
    color: "gray"}}>{item.AccountAttachedMobileNo}</p>
    
    <CCard style={{backgroundColor:"#e55353",
    opacity:"1.5",
    color:"white",
    width: "132px",
    textAlign: "center",
    fontSize: "12px",
    padding: "0px",
    position:"sticky",
    marginBottom:"0.5rem"
}}>
    Upto 50% CashBack
    </CCard>
    
    <CLink href={`tel:+${item.AccountAttachedMobileNo}`} style={{fontSize: "10px",
    border: "2px solid",
    padding: "3px",
    borderRadius: "4px"}}> 
    <FontAwesomeIcon icon={faPhoneAlt} style={{color:"blue"}}/>  Call Now

     </CLink> 
    
     <CButton onClick={()=>props.handleVdetails(item.id)} value="accept" color="success" className="addWalletbtn" style={{
                    width: "6.5rem",
                    height: "2rem",
                    borderBottomLeftRadius: "1rem",
                    borderTopLeftRadius: "1rem",
                    float:"right",
                    marginTop: "17px",
                    marginRight: "-23px"
                  }}>Stores  detail</CButton>
   
                  
           </CCol>
            
        </CCardBody>
        
      </CCard>
      ))}
      
        </div>
    );
}

export default Vendor;


