import React, { Component } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import './scss/style.scss';
import Register from './user/Pages/Register';
import ForgotPass from './CommonRoutes/ForgotPass';
import SignUp from './vendor/Pages/SignUp';
import UpdateProfile from './vendor/Pages/UpdateProfile';
import UserUpdateProfile from './user/Pages/UpdateProfile';
import Bankacard from "./user/Pages/Bankcard";
import CategorySlide from './user/Pages/CategorySlide';
import CategorySlide2 from "./user/Pages/CategorySlide2";
import SearchVendor from './user/Pages/SearchVendor';
// import Otp from './CommonRoutes/Otp';


const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

// Containers
const TheLayout = React.lazy(() => import('./containers/TheLayout'));
const Login = React.lazy(() => import('./login/Login'));
class App extends Component {

  render() {
    return (
      <BrowserRouter>
          <React.Suspense fallback={loading}>
            <Switch>
              <Route exact path="/" name="Login Page" render={props => <Login {...props}/>} />
              <Route exact path="/forgotpass" name="Forgot" render={props => <ForgotPass {...props}/>} />
              <Route exact path="/RegisterVendor" name="Register Page" render={props => <SignUp {...props}/>} />
              <Route exact path="/RegisterUser" name="Register Page" render={props => <Register {...props}/>} />
              {/* <Route exact path="/otp" name="Otp Page" render={props => <Otp {...props}/>} /> */}
              <Route exact path="/updateprofile/id" name="Register Page" render={props => <UpdateProfile {...props}/>} />
              <Route exact path="/userupdateprofile/id" name="Register Page" render={props => <UserUpdateProfile {...props}/>} />
              <Route exact path="/bankcard/id" name="bankcard" render={props => <Bankacard {...props}/>} />
              <Route exact path="/cat/id" name="sliderproduct" render={props => <CategorySlide {...props}/>} />
              <Route exact path="/catevendor/id" name="slider" render={props => <CategorySlide2 {...props}/>} />
              <Route exact path="/searchvendor/id" name="SearchVendor" render={props => <SearchVendor {...props}/>} />
              
              
              <Route path="/" name="Home" render={props => <TheLayout {...props}/>} />
            </Switch>
          </React.Suspense>
      </BrowserRouter>
    );
  }
}

export default App;
