import React, { Component } from 'react';
import {
    CCol,
    CRow,

} from '@coreui/react'
import { Base_URL, imgPath } from "../../CommonRoutes/Api"
import Vendor from '../component/Vendor';
import {  faArrowLeft, faUndoAlt } from '@fortawesome/free-solid-svg-icons';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';


class SearchVendor extends Component {
    constructor(props){
        super(props);
        this.state={
            vendorlist:[],

        }
    }

    componentDidMount(){   debugger
        fetch(Base_URL + "getvendorbyName/" + this.props.match.params.id).then((response) => {
            response.json().then((result) => {
                console.log("detail", result);
                this.setState({ vendorlist: result})
              
            })
        })
  
    }

    back(){
        this.props.history.push('/dashboard');     
      }

    render() {
        console.log(this.props)
        return (
            <div>
                <CRow style={{ margin: "-16px" }}>
                            <CCol md="12" lg="12" xl="12" className="content-center" style={{
                                backgroundColor: "#110730",
                                marginBottom: "0.5rem"
                            }}>
                             <Link href="#" onClick={()=>this.back()}> <FontAwesomeIcon icon={faArrowLeft} style={{marginLeft:"-4rem",color:"orange",float:"left"}} />  </Link> 
                                <h3 style={{ color: "orange" }} >Our Vendors</h3>
                            </CCol>
                        </CRow>
                      
                        {
                            this.state.vendorlist.data && this.state.vendorlist.data.length > 0 
                           ?
                               (
                        
                               /*  <Link href="#" onClick={() => this.handleVdetails(item.id)}> */
                                <Vendor   data={this.state.vendorlist.data} 
                                      //handleVdetails={this.handleVdetails}
                                    
                                />
                                /* </Link> */
                                
                            ):'No data Found'}
                            
                     
                             
                 </div>
        );
    }
}

export default SearchVendor;