import React, { Component } from 'react';
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import Modal from 'react-modal';
import { Base_URL,headers } from './Api';

const customStyles = {
  content : {
    backgroundColor       : '#202342',
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)'
  }
};
class ForgotPass extends Component {
  constructor(props){
    super(props)
    this.state={
      mobile:'',
      vfyotp:'',
      password:'',
      cpassword:'',
      modalIsOpen: false,
      mobmodalIsOpen:true,
      passmodalIsOpen:false,
      role:localStorage.getItem("role")
    }
  }


  openModal = () => {
    const { mobile} = this.state;
    if (mobile !=="") 
    {
      fetch(Base_URL + "verify_vendor_MOB/" +this.state.mobile).then((response) => {
        response.json().then((result) => {
          console.log( "sub",result);
          if(result.data === this.state.mobile){
            this.setState({
              submitted: true
            });
           this.openModal1();
          }
        })
      })
    }
  };
  generateOTP() {
    let digits = '0123456789';
    let OTP = '';
    for (let i = 0; i < 4; i++ ) {
        OTP += digits[Math.floor(Math.random() * 10)];
    }
    return OTP;
}

  openModal1() {
    let otp=this.generateOTP();
    this.setState({modalIsOpen: true,otp:otp});
    let mobile=this.state.mobile;
    fetch(Base_URL+"Otp_req", {
      method: 'POST',headers,
      body: JSON.stringify({otp,mobile})
      }).then((result) => {
      result.json().then((resp) => {
          console.log(result);
      })
      })
  }

  closeModal() {
    this.setState({modalIsOpen: false,passmodalIsOpen:false});
  }

  handlepassmodle(){
    if(this.state.otp === this.state.vfyotp){
      this.setState({passmodalIsOpen:true,modalIsOpen:false,mobmodalIsOpen:false})
    }
  }

  submitForm = () =>{
    let url = Base_URL+"forgot_pass_vendor/"+this.state.mobile;
    let role=this.state.role;
    let password=this.state.password
    let data = {role,password};
    fetch(url, {method: 'PUT',headers,body: JSON.stringify(data)
        }).then((result) => {
        result.json().then((resp) => {
            console.log(resp);
            if(result.status === "201"){
              this.closeModal();
              window.location.href='/';
            } 
        })
        })
    }

    render() {
        return (
    <div className="c-app c-default-layout flex-row align-items-center">
    <Modal
          isOpen={this.state.modalIsOpen}
          style={customStyles}
          contentLabel="Example Modal"
        >

<CContainer>
            <CRow className="justify-content-center">
                <CCol md="12" lg="12" xl="12">
                    <CCard className="mx-4">
                        <CCardBody className="p-4">
                        <h3>OTP Verification</h3>
  
                          <label>Enter OTP</label>
                            <CInputGroup className="mb-3" >
                            <CInputGroupPrepend>
                              <CInputGroupText>
                                <CIcon name="cil-user" />
                              </CInputGroupText>
                            </CInputGroupPrepend>
                            <CInput type="text" name="dname" placeholder="Enter Your OTP" autoComplete="false"
                                    onChange={(event) =>{this.setState({vfyotp:event.target.value})}}    />
                            </CInputGroup>
                            <CButton  type="submit" value="add" block color="primary" fullWidth={true} onClick={()=>this.handlepassmodle()}>Verify</CButton>
                        </CCardBody>
                    </CCard>
                </CCol>
            </CRow>
            </CContainer>
        </Modal>

{this.state.mobmodalIsOpen ?
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md="8">
            <CCardGroup>
              <CCard className="p-4">
                <CCardBody>
                  <CForm>
                    <h2>Forgot Password</h2>
                    <label>Mobile Number</label>
                  <CInputGroup className="mb-3">
                    <CInputGroupPrepend>
                      <CInputGroupText>
                        <CIcon name="cil-phone" />
                      </CInputGroupText>
                    </CInputGroupPrepend>
                    <CInput type="text"
                      name="mobile"
                      placeholder="Enter mobile number"
                      autoComplete="false"
                    value={this.state.mobile}
                    onChange={(event)=>(this.setState({mobile:event.target.value}))}
                    />
                      
                    </CInputGroup>
                    <span style={{color:"red"}}>Note: To reset your password,enter your registration mobile number</span>
                    <br/><br/> 
                       <CButton color="primary" className="px-4" onClick={()=>this.openModal()}>SEND OTP</CButton>
                  </CForm>
                </CCardBody>
              </CCard>
             
            </CCardGroup>
          </CCol>
        </CRow>
      </CContainer> :

     this.state.passmodalIsOpen ?
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md="8">
            <CCardGroup>
              <CCard className="p-4">
                <CCardBody>
                  <CForm>
                    {/* <h2>Forgot Password</h2> */}
                    <label>Password</label>
                  <CInputGroup className="mb-3">
                    <CInputGroupPrepend>
                      <CInputGroupText>
                        <CIcon name="cil-phone" />
                      </CInputGroupText>
                    </CInputGroupPrepend>
                    <CInput type="text"
                      name="mobile"
                      placeholder="Password"
                      autoComplete="false"
                    value={this.state.password}
                    onChange={(event)=>(this.setState({password:event.target.value}))}
                    />
                    </CInputGroup>

                    <label>Password</label>
                    <CInputGroup className="mb-3">
                    <CInputGroupPrepend>
                      <CInputGroupText>
                        <CIcon name="cil-phone" />
                      </CInputGroupText>
                    </CInputGroupPrepend>
                    <CInput type="text"
                      name="mobile"
                      placeholder="Confirm Password"
                      autoComplete="false"
                    value={this.state.cpassword}
                    onChange={(event)=>(this.setState({cpassword:event.target.value}))}
                    />
                      
                    </CInputGroup>
                    <br/><br/> 
                       <CButton color="primary" className="px-4" onClick={()=>this.submitForm()}>Submit</CButton>
                  </CForm>
                </CCardBody>
              </CCard>
             
            </CCardGroup>
          </CCol>
        </CRow>
      </CContainer> : ''}
    </div>

   /*  </div>
            </div> */
        );
    }
}


export default ForgotPass;   