import React from 'react';
import { ToastsStore } from "react-toasts";
import {
  CInputCheckbox,
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCol,
  CCardGroup,
  CTextarea,
  CContainer,
  CSelect,
  CFormSelect,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import CIcon from '@coreui/icons-react';
import PersonIcon from '@material-ui/icons/Person'
import { Base_URL, headers } from 'src/CommonRoutes/Api';
import Modal from 'react-modal';

const customStyles = {
  content : {
    backgroundColor       : '#202342',
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)'
  }
};


class Register extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      role: localStorage.getItem("role"),
      name: "",
      email: "",
      mobile: "",
      role: localStorage.getItem("role"),
      password: "",
      nameError: '',
      emailError: '',
      mobileError: "",
      roleError: "",
      refer_code:''

    }

  }

  openModal = () => {
    const { name, email1, mobile, city, phone } = this.state;
    this.setState({
      submitted: true
    });

    if (name !== "" && email1 !== "" && mobile !== "", city !== "" && phone !== "") {
      this.openModal1();
    }
  };

  generateOTP() {
    let digits = '0123456789';
    let OTP = '';
    for (let i = 0; i < 4; i++) {
      OTP += digits[Math.floor(Math.random() * 10)];
    }
    return OTP;
  }

  openModal1() {
    let otp = this.generateOTP();
    this.setState({ modalIsOpen: true, otp: otp });
    let mobile = this.state.mobile;
    fetch(Base_URL + "Otp_req", {
      method: 'POST', headers,
      body: JSON.stringify({ otp, mobile })
    }).then((result) => {
      result.json().then((resp) => {
        console.log(result);
      })
    })
  }

  closeModal() {
    this.setState({ modalIsOpen: false });
  }


  submitForm = () => {

    let url = Base_URL + "register";
    const data = {refer_code:this.state.refer_code, name: this.state.name, role: this.state.role, mobile: this.state.mobile, password: this.state.password, email: this.state.email, c_password: this.state.c_password }
    fetch(url, {
      method: 'POST',
      headers: {
        "Content-Type": "application/json",
        "Accept": "application/json",
      },
      body: JSON.stringify(data)
    }).then((result) => {
      result.json().then(() => {
        if(result.status === 200){
        alert("form has been submiteed");
        this.props.history.push('/');
        }
      }, (error) => console.log(error))

    })
  }

  handleCancle() {
    window.location.href = '/';
  }

  render() {
    return (
      <div className="c-app c-default-layout flex-row align-items-center">
      <Modal
          isOpen={this.state.modalIsOpen}
          style={customStyles}
          contentLabel="Example Modal"
        >

<CContainer>
            <CRow className="justify-content-center">
                <CCol md="12" lg="12" xl="12">
                    <CCard className="mx-4">
                        <CCardBody className="p-4">
                        <h3>OTP Verification</h3>
  
                          <label>Enter OTP</label>
                            <CInputGroup className="mb-3" >
                            <CInputGroupPrepend>
                              <CInputGroupText>
                                <CIcon name="cil-user" />
                              </CInputGroupText>
                            </CInputGroupPrepend>
                            <CInput type="text" name="dname" placeholder="Enter Your OTP" autoComplete="false"
                                    onChange={(event) =>{this.setState({vfyotp:event.target.value})}}    />
                            </CInputGroup>
                            <CButton  type="submit" value="add" block color="primary" fullWidth={true} onClick={()=>this.submitForm()}>Verify</CButton>
                        </CCardBody>
                    </CCard>
                </CCol>
            </CRow>
            </CContainer>
        </Modal>
        <CContainer>
          <CRow className="justify-content-center">
            <CCol md="9" lg="7" xl="6">

              <CCardGroup>
                <CCard className="p-4 " style={{ backgroundColor: '#110730', marginLeft: '-1rem', marginRight: '-.9rem', marginBottom: '-.1rem' }}>
                  {this.state.isLoggedIn ?
                    <span style={{ textAlign: 'right', marginTop: '-1rem', marginBottom: '.5rem', color: 'white' }} onClick={() => this.skip()}><b>Skip{`>>`}</b></span> : ''}

                  <CForm>
                    <center><PersonIcon style={{ backgroundColor: '#2962a3', borderRadius: '50px', width: '90px', height: '90px' }} /></center>
                    <center style={{ color: 'white', marginTop: '.5rem', marginBottom: '2rem' }}><h3><b>CUSTOMER</b></h3></center>
                  </CForm>

                  <hr style={{ marginLeft: '15px', marginRight: '15px', backgroundColor: 'white' }} /><br />

                  <CCol xs="12">
                    <CInputGroup className="mb-3">
                      <CInput type="text" name="username" placeholder="Name" autoComplete="false"
                        value={this.state.name} onChange={(e) => this.setState({ name: e.target.value })} style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }} />
                    </CInputGroup>
                  </CCol>

                  <CCol xs="12">
                    <CInputGroup className="mb-3 ">
                      <CInput type="tel" name="username" placeholder="Mobile Number" autoComplete="false" required="required"
                        value={this.state.mobile} onChange={(e) => this.setState({ mobile: e.target.value })} style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }} />
                    </CInputGroup>
                  </CCol>
                  <CCol xs="12">
                    <CInputGroup className="mb-3">
                      <CInput type="text" name="username" placeholder="Email" autoComplete="false"
                        value={this.state.email} onChange={(e) => this.setState({ email: e.target.value })} style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }} />
                    </CInputGroup>
                  </CCol>

                  <CCol xs="12">
                    <CInputGroup className="mb-3 ">
                      <CInput type="password" name="username" placeholder="Password" autoComplete="false"
                        value={this.state.password} onChange={(e) => this.setState({ password: e.target.value })} style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }} />
                    </CInputGroup>
                  </CCol>
                  <CCol xs="12">
                    <CInputGroup className="mb-3">
                      <CInput type="password" name="username" placeholder="Re-Enter Password" autoComplete="false"
                        value={this.state.password} onChange={(e) => this.setState({ password: e.target.value })} style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }} />
                    </CInputGroup>
                  </CCol>

                  <div> {this.state.passwordError ? (
                    <p style={{ color: "red" }}> {this.state.passwordError}</p>) : null}  </div>

                    <CCol xs="12">
                    <CInputGroup className="mb-3">
                      <CInput type="text" name="username" placeholder="Referral Code" autoComplete="false"
                        value={this.state.refer_code} onChange={(e) => this.setState({ refer_code: e.target.value })} style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }} />
                    </CInputGroup>
                  </CCol>
                 
                  <hr style={{ marginLeft: '15px', marginRight: '15px', backgroundColor: 'white' }} /><br />

                  <CRow>
                    <CCol xs="12" style={{ textAlign: 'center' }}>
                      <CButton onClick={() => this.openModal()} style={{ backgroundColor: 'orange', width: '100%', borderRadius: '20px' }}
                        className="px-4"><b>REGISTER</b></CButton>
                    </CCol>
                  </CRow><br />
                  <CRow>
                    <CCol xs="12" style={{ textAlign: 'center' }}>
                      <CButton onClick={() => this.handleCancle()} style={{ backgroundColor: 'whitesmoke', width: '100%', borderRadius: '20px' }}
                        className="px-4"><b>CANCEL</b></CButton>
                    </CCol>
                  </CRow>
                </CCard>
              </CCardGroup>

            </CCol>
          </CRow>
        </CContainer>


      </div>
    )
  }
}
export default Register