import React from 'react';
import {
    CButton,
    CCard,
    CCardBody,
    CCol,
    CContainer,
    CInput,
    CInputGroup,
    CRow
} from '@coreui/react'
import "../../scss/Wallet.scss";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faExclamationCircle} from '@fortawesome/free-solid-svg-icons';
import { Component } from 'react';
import {Base_URL} from "../../CommonRoutes/Api"
import BlockUI from 'src/CommonRoutes/BlockUI/BlockUI';


class Bankcard extends Component {
  constructor(props){
    super(props);
    this.state={
      Account_holder_name:"",
      Account_no:"",
      c_accountno:"",
      Bank_Name:"",
      ifsc_code:"",
      email:"",
      mobile:"",
      nameError:'',
      emailError:'',
      mobileError:"",
      roleError:"",
      profile:[],
      profile1:[],
      id:localStorage.getItem("user_id"),
      blocking:false
      }

    }

    componentDidMount() { 


      if((!localStorage.getItem('token')) || (!localStorage.getItem('role')) || (!localStorage.getItem('user_id')))
      {
        this.props.history.push("/");
      }
      
      this.setState({blocking:true})
      fetch(Base_URL + "getuserbyid/" +this.state.id).then((response) => {
      response.json().then((result) => {
          console.log("result",result);
          this.setState({profile:result,blocking:false})
          setTimeout(
            () => this.setState({ position: 1,blocking:false }), 
            3000
          );
          console.log("profile",this.state.profile);
          console.log("profile1",this.state.profile[0]);
          console.log("profile2",this.state.profile[1][0]);
          this.setState({profile1:this.state.profile[0],profile2:this.state.profile[1][0]})
           this.setState({
            name:this.state.profile1.name,
            mobile:this.state.profile1.mobile,
            email:this.state.profile1.email,
            id:this.state.profile1.id
          })  
      }) 
      
  })        
} 


   

update()

{ 
  const data = { Bank_Name:this.state.Bank_Name,Account_holder_name:this.state.Account_holder_name,Account_no:this.state.Account_no,IFSC_code:this.state.ifsc_code}
    fetch(Base_URL + "addbank_details/" + this.state.id,
    {
        method:"Put",
        headers: {
            
            'Content-Type': 'application/json',
          },
        body:JSON.stringify(data)
    }).then((result)=> {
             result.json().then((resp)=> {
         alert("data is updated");
         this.props.history.push('/wallet');  

        })

    })
     
 } 
 
   cancel(){
     this.setState({
      Account_holder_name:"",
      Account_no:"",
      c_accountno:"",
      Bank_Name:"",
      ifsc_code:"",
      email:"",
      mobile:"",
     })
   }  


 

  render(){
    return ( 
        <div className="c-app c-default-layout flex-row align-items-center" >
    <CContainer style={{padding:"10px",marginTop:"-1.6rem"}}>
      <CRow className="justify-content-center">
        <CCol md="12" lg="12" xl="12" style={{ padding: "0px"}}>
       
          <CCard className="mx-10"  style={{ backgroundColor: '#110730', marginLeft: '-1rem', marginRight: '-.6rem', marginBottom: '-.1rem' }}>
          <center><h4 style={{color:"orange",marginTop:"1rem"}}>-Add Bank Details-</h4></center>
            <CCardBody className="p-10">
              {/* <CForm >  */}
              <label style={{color:"white"}}>Name</label>
              <CInputGroup className="mb-3" >
                <CInput type="text"
                  name="Account_holder_name"
                  placeholder=" Account_Holder_Name"
                  autoComplete="false"
                  value={this.state.Account_holder_name}
                   onChange={(event) =>{this.setState({Account_holder_name:event.target.value})}}
                   style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                />
              </CInputGroup>
              <label style={{color:"white"}}>Account Number</label>
              <CInputGroup className="mb-3" >
                <CInput type="text"
                  name="Account_no"
                  placeholder="Account Number"
                  autoComplete="false"
                   value={this.state.Account_no}
                   onChange={(event) =>{this.setState({Account_no:event.target.value})}}
                   style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                />
              </CInputGroup>
              <label style={{color:"white"}}>Confirm Account Number</label>
              <CInputGroup className="mb-3" >
                <CInput type="text"
                  name="c_accountno"
                  placeholder="Confirm Account Number"
                  autoComplete="false"
                   value={this.state.c_accountno}
                    onChange={(event) =>{this.setState({c_accountno:event.target.value})}}
                    style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                />
              </CInputGroup>
              <label style={{color:"white"}}>Bank Name</label>
              <CInputGroup className="mb-3">
                <CInput type="text"
                  name="Bank_Name"
                  placeholder="Bank Name"
                  autoComplete="false"
                  value={this.state.Bank_Name}
                  onChange={(event=>{this.setState({Bank_Name:event.target.value})})}
                  style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                />
              </CInputGroup>
              <label style={{color:"white"}}>IFSC Code</label>
              <CInputGroup className="mb-3">
                <CInput type="text"
                  name="ifsc_code"
                  placeholder="IFSC Code"
                  autoComplete="false"
                  value={this.state.ifsc_code}
                   onChange={(event=>(this.setState({ifsc_code:event.target.value})))}
                   style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                />
              </CInputGroup>
              <label style={{color:"white"}}>Email</label>
              <CInputGroup className="mb-3">
                <CInput type="email"
                  name="email"
                  placeholder="Email"
                  autoComplete="false"
                   value={this.state.email}
                   // onChange={(event)=>(this.setState({email:event.target.value}))}
                   style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                />

              </CInputGroup>
              <label style={{color:"white"}}>Mobile Number</label>
              <CInputGroup className="mb-3">
                <CInput type="text"
                  name="mobile"
                  placeholder="Mobile Number"
                  autoComplete="false"
                   value={this.state.mobile}
                  onChange={(event=>(this.setState({mobile:event.target.value})))}
                  style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                />
              </CInputGroup>
              <p style={{color:"red"}}> <FontAwesomeIcon icon={faExclamationCircle} style={{ fontSize: "18px"}} color="red"/>
                                                  In case your application rejected,please make sure all information correct </p>

              <CButton
                type="submit"
                value="add"
                block color="warning"
                fullWidth={true}
               onClick={()=>this.update()}
               style={{color:"white"}}
              >save</CButton>
                <CButton
                type="reset"
                value="add"
                block color="light"
                fullWidth={true}
                onClick={()=>this.cancel()}
                style={{color:"#3B5998"}}
              >cancel</CButton>
              {/*   </CForm>  */}
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </CContainer>
    <BlockUI blocking={this.state.blocking} />
  </div>   
    );
  }
}

export default Bankcard;

