import React,{ useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
    CButton,
    CCol,
    CRow,
} from '@coreui/react'
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import CallIcon from '@material-ui/icons/Call';
import RoomIcon from '@material-ui/icons/Room';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import ShareIcon from '@material-ui/icons/Share';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import Grid from '@material-ui/core/Grid';
import Fab from '@material-ui/core/Fab';
import UploadPhoto from './UploadPhoto';
import CIcon from '@coreui/icons-react'
import profile from '../../assets/img/pro.jpeg';

const useStyles = makeStyles((theme) => ({
    paper: {
        padding: theme.spacing(2),
        color: theme.palette.text.secondary,
    },
    root: {

        marginTop: 9,
        marginLeft: -1,
        marginRight: -1,
    },
    media: {
        height: 170,
    },
    avtar: {
        padding: '0 30px',
    }


}));

export default function MediaCard(props) {
    const classes = useStyles();
    const [lat, setLat] = useState();
    const [long, setLong] = useState();

    useEffect(() => {
        navigator.geolocation.getCurrentPosition(function (position) {
            console.log("Latitude is :", position.coords.latitude);
            console.log("Longitude is :", position.coords.longitude);
            setLat(position.coords.latitude)
            setLong(position.coords.longitude)
        });
    });

    return (
        <Card className={classes.root} >
            <CardActionArea>
                <CardMedia
                    className={classes.media}
                    image={profile}
                    title="Contemplative Reptile"
                />
                <CardContent>
                    <Typography gutterBottom variant="h5" component="h1">
                        Dr. Rubina Rathi's Medicure Multispeciality Clinic
                    </Typography>
                    <Typography variant="body2" color="textSecondary" component="p">
                        Ramdas Peth - 4.5 km
                    </Typography><br />
                    <Typography variant="body2" color="textSecondary" component="p">
                        <StarBorderIcon /><StarBorderIcon />
                        <StarBorderIcon /><StarBorderIcon /><StarBorderIcon /><StarBorderIcon />
                        <br />4.9 Rating <br /><br /> Fees Rs 500
                    </Typography>
                </CardContent>
            </CardActionArea>
            <CardActions >

                <Grid container alignItems="center" direction="row" justify="center">
                    <Fab aria-label="add" style={{ backgroundColor: 'white' }}>
                        <CallIcon style={{ color: 'blue' }} />
                    </Fab>
                </Grid>

                <Grid container alignItems="center" direction="row" justify="center" >
                    <Fab aria-label="add" style={{ backgroundColor: 'white' }}>
                    <a href={`https://www.google.com/maps/search/?api=1&query=${props.lat}%2C-${props.long}`}>
                        <RoomIcon style={{ color: 'blue' }} />
                        </a>
                    </Fab>
                </Grid>

                <Grid container alignItems="center" direction="row" justify="center" >
                    <Fab aria-label="add" style={{ backgroundColor: 'white' }}>
                    <a href={"https://api.whatsapp.com/send?phone=7066112512??text=Send20%a20%quote"}> 
                        <WhatsAppIcon style={{ color: 'green' }} />
                        </a>
                    </Fab>
                </Grid>

                <Grid container alignItems="center" direction="row" justify="center" >
                    <Fab aria-label="add" label="Share" style={{ backgroundColor: 'white' }}>
                        <ShareIcon style={{ color: 'blue' }} />
                    </Fab>
                </Grid>
            </CardActions>

            <CRow>
                <CCol xs="12" style={{ textAlign: 'center', marginTop: '20px', marginBottom: '20px' }}>
                    <CButton style={{ backgroundColor: 'white', width: '97%', borderRadius: '20px', color: 'blue' }}
                        className="px-4"><b>ENQUIRE NOW</b></CButton>
                </CCol>
            </CRow>
            <br />
            <UploadPhoto />
            <br />

            <div>
                <h4>Rate this</h4>
                <Grid container spacing={3} style={{ textAlign: 'center' }}>
                    <Grid item xs={2}>
                        <Fab aria-label="add" style={{ backgroundColor: 'white', marginBottom: '1rem' }}>
                            <StarBorderIcon />
                        </Fab>
                    </Grid>
                    <Grid item xs={2}>
                        <Fab aria-label="add" style={{ backgroundColor: 'white', marginBottom: '1rem' }}>
                            <StarBorderIcon />
                        </Fab>
                    </Grid>
                    <Grid item xs={2}>
                        <Fab aria-label="add" style={{ backgroundColor: 'white', marginBottom: '1rem' }}>
                            <StarBorderIcon />
                        </Fab>
                    </Grid>
                    <Grid item xs={2}>
                        <Fab aria-label="add" label="Share" style={{ backgroundColor: 'white', marginBottom: '1rem' }}>
                            <StarBorderIcon />
                        </Fab>
                    </Grid>
                    <Grid item xs={2}>
                        <Fab aria-label="add" label="Share" style={{ backgroundColor: 'white', marginBottom: '1rem' }}>
                            <StarBorderIcon />
                        </Fab>
                    </Grid>

                </Grid>
                <hr />
                <h4>Hours and Services May Differ</h4>
                <hr />
                <h4>Hours and Services May Differ</h4>
                <hr />
                <h4>Hours and Services May Differ</h4>
                <hr />
                <h4>Hours and Services May Differ</h4>
                <hr />
            </div>


        </Card>
    );
}
